import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import React, { useState, useEffect } from "react";
import axios from "axios";
import Button from 'react-bootstrap/Button';
export default function AddSourceModal(props) {
  const [name, setName] = useState("");
  const [url, setUrl] = useState("");
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => {console.log("click");setShow(true);}
  const dest_url =
  (process.env.REACT_APP_IP ?? "127.0.0.1") +
  ":" +
  (process.env.REACT_APP_PORT ?? "538");
  return (
      <>
    <Button variant="primary" onClick={handleShow}>
    Add new general source
  </Button>
    <Modal
      title={props.title}
      show={show}
    >
      <Form>
        <Form.Group controlId="name">
          <Form.Label>source name</Form.Label>
          <Form.Control type="name" placeholder="" value={name} onChange={(event)=>setName(event.target.value)}/>
        </Form.Group>

        <Form.Group controlId="url">
          <Form.Label>source name</Form.Label>
          <Form.Control type="url" placeholder="" value={url} onChange={(event)=>setUrl(event.target.value)} />
        </Form.Group>
        <Button variant="primary" type="submit" onClick={()=>{
            console.log(name);
            axios({url:`http://${dest_url}/${props.source}`,method: 'post',data:{'name':name, 'url':url},headers:{"content-type":"application/json"}}).then((response)=>{console.log(response)})}}>
          Submit
        </Button>
      </Form>
    </Modal>
    </>
  );
}
